.root {
  overflow: auto;
  height: 55vh;
  width: 55vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

@media (max-aspect-ratio: 1/2) {
  .root {
    height: 98vw;
    width: 98vw;
  }
}

.row {
  display: flex;
  flex-direction: row;
  height: calc(100% / 7);
  width: 100%;
}

.large {
  height: 93vh;
  width: 93vh;
}
